.itemGrid{
    width: 100%;
    display: flex;
    .card{
        width: 100%;
        background-color: map-get($map: $paleta, $key: 'cards');
        height: auto;
        border-radius: $borderRadiusLg;
        text-decoration: none;
        box-shadow: $sombra;
        figure{
            margin: 0;
            .cardImage{
                object-fit: cover;
                height: 175px;
                width: 100%;
                border-top-left-radius: $borderRadiusLg;
                border-top-right-radius: $borderRadiusLg;
            }
            .cardText{
                height: auto;
                padding: $pXXs $pXs $pXs $pXs;
                h4{
                    text-shadow: -0.5px 0.5px map-get($map: $paleta, $key: "sombras");
                    margin-bottom: $mSmall;
                }
                figcaption{
                    @include texto($textSmall, 2rem, $fontLight, center);
                    font-style: italic;
                }
            }
        }
        &:active {
            background-color: map-get($map: $paleta, $key: "terceario");
            transform: scale(0.99);
            .categorias{
                .categoria{
                    background-color: map-get($map: $paleta, $key: 'tercearioActive');
                }
            }
        }
    }
}
.categorias{
    @include flex (row, start, center);
    flex-wrap: wrap;
    width: 100%;
    padding: 0 $pSmall $pSmall $pSmall;
    gap: $pXs;
}
.categoria{
    background-color: map-get($map: $paleta, $key: 'terceario');
    padding: $pXXs;
    border-radius: $borderRadiusSm;
    box-shadow: 0 2px 4px 0 #1e1e1e33,;
}

@media (width >= 992px) {
    .itemGrid{
        .card{
            figure{
                .cardImage{
                    height: 200px;
                }
                .cardText{
                    padding: $pXs $pSmall $pSmall $pSmall;
                    h4{
                        text-shadow: -0.5px 0.5px map-get($map: $paleta, $key: "sombras");
                    }
                    figcaption{
                        @include texto($textSmall, 2rem, $fontLight, center);
                        font-style: italic;
                    }
                }
            }
            &:hover {
                background-color: map-get($map: $paleta, $key: "terceario");
                .categorias{
                    .categoria{
                        background-color: map-get($map: $paleta, $key: 'tercearioActive');
                    }
                }
            }
        }
    }
    .categorias{
        @include flex (row, start, center);
        flex-wrap: wrap;
        gap: $mXxs;
        width: 100%;
        padding: 0 $pSmall $pSmall $pSmall;
    }
    .categoria{
        background-color: map-get($map: $paleta, $key: 'terceario');
        padding: $pXXs;
        border-radius: $borderRadiusSm;
    }
}
