footer{
    @include flex (column, center, center);
    background-color: map-get($map: $paleta, $key: 'pie');
    padding: $pLarge;
    padding-bottom: $pSmall;
    gap: $mLarge;
    margin-top: $mLarge;
    .contFooter{
        div{
            margin-top: $mLarge;
            ul{
                @include flex (column, center, center);
                list-style: none;
                padding: 0;
                margin-top: $mMedium;
                gap: $mSmall;
                li{
                    @include texto ($textSmall, 2rem, $fontLight, center);
                    a{
                        width: 100%;
                        text-decoration: none;
                        color: map-get($map: $paleta, $key: 'textos');
                        &:active{
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
        .div1Footer{
            margin-top: 0;
        }
    }
    .derechoAutor{
        @include texto ($textSmall, 1rem, $fontLight, center);
    }
}

@media (width >= 992px){
    footer{
        gap: $mSmall;
        .contFooter{
            width: 100%;
            @include flex (row, space-evenly, start);
            div{
                margin: 0;
                h4{
                    text-align: left;
                }
                ul{
                    @include flex (column, center, start);
                    gap: 5px;
                }
            }
        }
    }
}