header {
	@include flex(row, center, center);
	background-color: map-get($map: $paleta, $key: "secundario");
	padding: $pMedium;
	img {
		height: 100px;
	}
	z-index: 1000;
	a {
		transition: 0.1s;
		&:active {
			transform: scale(0.95);
		}
	}
}

.navegador {
	@include flex(column, center, center);
	background-color: map-get($map: $paleta, $key: "primario");
	height: auto;
	width: 100%;
	position: sticky;
	top: 0;
	padding: 20px 10px;
	box-shadow: $sombra;
	z-index: 999;
	ul {
		@include flex(row, space-evenly, center);
		flex-wrap: nowrap;
		list-style: none;
		width: 100%;
		padding: 0;
		margin: 0;
		li {
			width: 100px;
			a {
				@include flex(row, center, center);
				width: 100%;
				height: 100%;
				.icoNav {
					font-size: 2rem;
				}
				p {
					display: none;
				}
			}
		}
		.logoNav {
			display: none;
		}
	}
}

@media (width >= 992px) {
	header {
		display: none;
	}
	.navegador {
		height: 60px;
		padding: 0 $mLarge;
		ul {
			@include flex(row, start, center);
			height: 100%;
			li {
				@include flex(row, center, center);
				width: 100%;
				height: 100%;
				a {
					text-decoration: none;
					transition: all 0.3s;
					p {
						display: block;
						margin: 0;
						margin-left: 10px;
					}
				}
			}
			.logoNav {
				display: block;
				margin-right: $mLarge;
				transition: all 0.3s;
				width: auto;
				a {
					img {
						width: 50px;
					}
				}
				&:hover {
					transform: scale(1.1);
					rotate: -3deg;
				}
			}
			.elementoNav {
				&:hover {
					background-color: map-get(
						$map: $paleta,
						$key: "primarioHover"
					);
					a {
						transform: scale(1.05);
					}
				}
			}
			.idiomaNav {
				width: auto;
				margin: 0;
				a {
					width: 50px;
				}
				&:hover {
					a {
						transform: rotateY(180deg);
					}
				}
			}
		}
	}
}
