.todosLosArticulos{
    @extend .articulosInicio;
    margin: 0;
}

.listaCategorias{
    background-color: map-get($map: $paleta, $key: 'secundario');
    margin: $mMedium 0;
    padding: $pXs $pMedium;
    border-radius: $borderRadiusLg;
    display: flex;
    flex-direction: column;
}
.divCategorias{
    display: flex;
    flex-wrap: wrap;
    gap: $mXs;
}
.cat{
    @extend .categoria;
    padding: $pXXs $pXs;
    text-decoration: none;
    p{
        margin: 0;
    }
    &:active{
        transform: scale(0.95);
    }
}

@media (width >= 992px){
    .cat{
        &:hover{
            background-color: map-get($map: $paleta, $key: 'primario');
        }
    }
}