/* PAGINA PRINCIPAL */
.tituloPrincipal{
	@include titulo (4rem, $fontBold, 0, center);
}
.inicio {
	margin: $mLarge $pLateralMov;
	.textoInicio {
		@include texto($textSmall, 3rem, $fontRegular, center);
		margin: $mMedium;
		text-shadow: -0.5px 0.5px map-get($map: $paleta, $key: "sombras");
	}
	.botonesInicio {
		@include flex(column, center, center);
		gap: 20px;
		a {
			padding: 10px 15px;
			text-decoration: none;
			border-radius: $borderRadiusMd;
			background-color: map-get($map: $paleta, $key: "primario");
			box-shadow: $sombra;
			p {
				margin: 0;
				text-align: center;
				text-shadow: -0.5px 0.5px
					map-get($map: $paleta, $key: "sombras");
				font-weight: $fontRegular;
			}
			&:active {
				transform: scale(0.99);
				background-color: map-get($map: $paleta, $key: 'primarioHover');
			}
		}
		.btnAcerca {
			background-color: map-get($map: $paleta, $key: "terceario");
		}
	}
}

.ultimos {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: map-get($map: $paleta, $key: "sections");
	padding: $pXs $pXs $pMedium $pXs;
	box-shadow: $sombra;
	margin-bottom: $mLarge;
	border-radius: $borderRadiusSm;
	h2 {
		margin: $mMedium $mMedium;
	}
}
.articulosInicio {
	@include flex (column, center, start);
	gap: 30px;
}

.verMasArticulos{
	@include flex(row, center, center);
	text-decoration: none;
	margin-top: $mMedium;
	height: 40px;
	width: 110px;
	background-color: map-get($map: $paleta, $key: 'primario');
	border-radius: $borderRadiusMd;
	box-shadow: $sombra;
	p{
		@include texto($textMedium, 1.5rem, $fontRegular, center);
		margin: 0;
		text-shadow: -0.5px 0.5px map-get($map: $paleta, $key: "sombras");
	}
	&:active {
		transform: scale(0.99);
		background-color: map-get($map: $paleta, $key: 'primarioHover');
	}
}

@media (width >=576px){
	.ultimos{
		margin: 0 $pLateralMov;
		padding: $pMedium $pMedium $pLarge $pMedium;
	}
}

@media (width >=768px){
	.ultimos{
		padding: $pSmall $pSmall $pLarge $pSmall;
		margin: 0;
	}
	.articulosInicio{
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 30px;
	}
	.verMasArticulos{
		margin-top: $mLarge;
	}
}

@media (width >= 992px) {
    /* PAGINA PRINCIPAL */
    .inicio{
        margin: $mLarge $mLarge;
        .textoInicio{
            @include texto ($textMedium, 4rem, $fontRegular, center);
            margin: $mLarge;
            text-shadow: -1px 1px map-get($map: $paleta, $key: 'sombras');
        }
        .botonesInicio{
            @include flex (row, center, center);
            gap: 30px;
            a{
                &:hover{
                    background-color: map-get($map: $paleta, $key: 'primarioHover');
                }
            }
            .btnAcerca{
                background-color: map-get($map: $paleta, $key: 'terceario');
                &:hover{
                    background-color: map-get($map: $paleta, $key: 'tercearioActive');
                }
            }
        }
    }
    .ultimos{
        margin: $mLarge 0 0 0;
        padding: $pLarge;
        h2{
            margin-top: 0;
            margin-bottom: $mLarge;
        }
    }
	.articulosInicio{
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 30px 50px;
	}

	.verMasArticulos{
		&:hover{
			background-color: map-get($map: $paleta, $key: 'primarioHover');
		}
	}
}

@media (width >= 1200px){
	.ultimos{
		padding: $pLarge $pMedium;
	}
	.articulosInicio{
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 30px 35px;
	}
}