//colores
$paleta: (
	"primario": rgb(10, 193, 142),
	"primarioHover": rgb(72, 205, 167),
	"secundario": rgba(85, 86, 85, 0.6),
	"terceario": rgb(128, 128, 128),
	"tercearioActive": rgb(148, 147, 147),
	"pie": rgb(36, 36, 36),
	"cards": rgb(128, 128, 128, 0.5),
	"sections": rgba(255, 255, 255, 0.05),
	"fondo": rgba(60, 60, 60, 0.5),
	"textos": rgb(255, 255, 255),
	"sombras": rgb(0, 0, 0),
);

$sombra: 0 4px 8px 0 #1e1e1e33, 0 6px 20px 0 #1e1e1e33;

//fonts
$mainFont: "Noto Sans", sans-serif;
$titleFont: "Noto Sans", sans-serif;
$codeFont: "Noto Sans Mono", monospace;

//sizing
$h1Size: 3rem;
$h2Size: 2.6rem;
$h3Size: 2rem;
$h4Size: 1.8rem;
$h5Size: 1.6rem;

$textSmall: 1.4rem;
$textMedium: 1.6rem;
$textLarge: 2rem;

//font weight
$fontLight: 100;
$fontRegular: 400;
$fontBold: 700;

//padding
$pLateralMov: 5%;
$pLateralPc: 10%;

$pXXs: 5px;
$pXs: 10px;
$pSmall: 15px;
$pMedium: 20px;
$pLarge: 40px;
$pXl: 80px;

// margins
$mXxs: 5px;
$mXs: 10px;
$mSmall: 15px;
$mMedium: 20px;
$mLarge: 40px;
$mXl: 60px;

//border radius
$borderRadiusSm: 5px;
$borderRadiusMd: 8px;
$borderRadiusLg: 15px;
