/* INDEX IDIOMAS */
.idiomasBody {
	width: 100%;
	header{
		display: block;
		width: 100%;
		height: auto;
		.logoIndexTop{
			@include flex (row, center, center);
			width: 100%;
			height: 100%;
			img{
				width: 100%;
				height: 100%;
			}
		}
		.h1Index {
			display: none;
			height: 80px;
			padding: 15px;
			background-color: map-get($map: $paleta, $key: "secundario");
			padding: $pMedium;
		}
	}
	.h2Index {
		width: 100%;
		background-color: map-get($map: $paleta, $key: "secundario");
		padding: $pMedium;
	}
	.idiomasMain {
		display: grid;
		gap: 10px;
		margin-bottom: $mSmall;
		padding: 0;
		grid-template-areas:
			"bca"
			"mex"
			"fra"
			"usa"
			"ale"
			"ita"
			"por";
		.mexIco {
			grid-area: mex;
		}
		.fraIco {
			grid-area: fra;
		}
		.usaIco {
			grid-area: usa;
		}
		.bcaIco {
			grid-area: bca;
			display: none;
		}
		.aleIco {
			grid-area: ale;
			display: none;
		}
		.itaIco {
			grid-area: ita;
			display: none;
		}
		.porIco {
			grid-area: por;
			display: none;
		}
		div {
			@include flex(row, center, center);
			a {
				@include flex(row, start, center);
				text-decoration: none;
				border-radius: $borderRadiusMd;
				padding: 5px 10px;
				img {
					width: 30px;
					margin-right: $mXs;
				}
				&:active {
					background-color: map-get(
						$map: $paleta,
						$key: "tercearioActive"
					);
					box-shadow: $sombra;
				}
			}
		}
	}
}

@media (width >= 576px){
	.idiomasBody{
		header{
			.logoIndexTop{
				padding: 0 $pLateralPc;
				@include flex (row, center, center);
				width: 100%;
				height: 100%;
				img{
					width: 100%;
					height: 100%;
				}
			}
		}
	}
}

@media (width >= 992px){
	.idiomasBody {
		@include flex(column, space-between, center);
		header{
			padding: 0;
			.logoIndexTop{
				display: none;
			}
			.h1Index {
				display: block;
			}
		}
		.idiomasMain {
			margin-top: $mSmall;
			width: 40%;
			grid-gap: 5px 5px;
			grid-template-areas: 
			'bca bca bca'
			'mex fra usa';
			.bcaIco {
				display: block;
				@include flex(row, center, center);
				width: 100%;
				margin: 0;
				padding: 0;
				background-color: transparent;
				width: auto;
				img {
					width: 50vh;
				}
			}
			.mexIco {
				width: 100%;
				@include flex(row, center, center);
			}
			.fraIco {
				width: 100%;
				@include flex(row, center, center);
			}
			.usaIco {
				width: 100%;
				@include flex(row, center, center);
			}
			.aleIco {
				width: 100%;
				@include flex(row, start, center);
				display: none;
			}
			.itaIco {
				width: 100%;
				@include flex(row, start, center);
				display: none;
			}
			.porIco {
				width: 100%;
				@include flex(row, start, center);
				display: none;
			}
			div {
				@include flex(row, start, center);
				a {
					transition: 0.25s;
					&:hover {
						transform: scale(1.2);
						background-color: map-get(
							$map: $paleta,
							$key: "tercearioActive"
						);
						box-shadow: $sombra;
					}
				}
			}
		}
		.h2Index {
			padding: $mMedium 0;
		}
	}
}