@mixin titulo ($size, $weight, $margin, $align){
    font-family: $titleFont;
    font-weight: $weight;
    font-size: $size;
    margin: $margin;
    text-align: $align;
}

@mixin texto ($size, $height, $weight, $txt-align){
    font-size: $size;
    font-weight: $weight;
    line-height: $height;
    text-align: $txt-align;
}

@mixin flex ($direction, $justify, $align){
    display: flex;
    flex-direction: $direction;
    justify-content: $justify;
    align-items: $align;
}