/* GENERALES */
* {
	box-sizing: border-box;
	font-family: $mainFont;
	color: map-get($map: $paleta, $key: "textos");
}

html{
    font-size: 62.5%;
    height: 100%;
    background-image: url(../img/fondos/subpattern_prism.webp);
	padding: 0;
}

body{
	background-attachment: fixed;
	background-color: map-get($map: $paleta, $key: 'fondo');
	padding: 0;
	margin: 0;
	position: relative;
    min-height: 100%;
    width: 100%;
}

main{
	padding: 0 $pLateralMov;
}

h1 {
	@include titulo($h1Size, $fontBold, 0, center);
    text-shadow: -2px 2px map-get($map: $paleta, $key: "sombras");
}

h2 {
	@include titulo($h2Size, $fontBold, 0, center);
    text-shadow: -1px 2px map-get($map: $paleta, $key: "sombras");
}

h3 {
	@include titulo($h3Size, $fontBold, 0, center);
    text-shadow: -1px 2px map-get($map: $paleta, $key: "sombras");
}

h4 {
	@include titulo($h4Size, $fontBold, 0, center);
}

h5 {
	@include titulo($h5Size, $fontLight, 0, center);
}

p {
	@include texto($textMedium, 2.2rem, $fontRegular, start);
}

strong{
    @include texto ($textMedium, 2.2rem, $fontBold, start);
    color: map-get($map: $paleta, $key: 'primario');
}

code{
    @include texto ($textMedium, 2.2rem, $fontLight, start);
    font-family: $codeFont;
}

.verde {
	color: map-get($map: $paleta, $key: "primario");
}

.separador{
    width: 100%;
    margin: 10px 0;
    border: none;
    border-top: 1px solid rgba(255,255,255,.2);
}

@media (width >= 992px) {
	main{
		padding: $pLarge $pLateralPc;
	}
    div{
        /* width */
        ::-webkit-scrollbar {
            height: 10px;
        }
        
        /* Track */
        ::-webkit-scrollbar-track {
            box-shadow: inset 0 0 5px map-get($map: $paleta, $key:'pie'); 
            border-radius: 10px;
        }
        
        /* Handle */
        ::-webkit-scrollbar-thumb {
            background: map-get($map: $paleta, $key:'primario'); 
            border-radius: 10px;
            box-shadow: inset 0 0 5px map-get($map: $paleta, $key:'pie');
        }
        
        /* Handle on hover */
        ::-webkit-scrollbar-thumb:hover {
            background: map-get($map: $paleta, $key:'primarioHover'); 
        }
    }
}
