.mainPost{
    padding: 0;
    .contenedorPost{
        .portadaPost{
            width: 100%;
            margin-bottom: $mXs;
            img{
                width: 100%;
            }
        }
        .contenidoPost{
            @include flex (column, center, center);
            padding: 0 $pLateralMov;
            margin: $mLarge 0 0 0;
            width: auto;
            .fechaPost{
                @include texto($textSmall, 1.2rem, $fontLight, end);
                font-style: italic;
                margin-bottom: $mXs;
            }
            h1{
                margin-bottom: $mMedium;
            }
            h2{
                margin: $mLarge 0 $mMedium 0;
            }
            h3{
                margin: $mLarge 0 $mMedium 0;
            }
            p{
                width: 100%;
                margin-top: 0;
            }
            figure{
                @include flex (column, center, center);
                width: 100%;
                margin: 0;
                div{
                    @include flex (column, center, start);
                    img{
                        width: 100%;
                        border-radius: $borderRadiusMd;
                    }
                    figcaption{
                        color: map-get($map: $paleta, $key: 'textos');
                        font-style: italic;
                    }
                }
                margin-bottom: $mMedium;
            }
            ul, ol{
                width: 100%;
                padding: 0 10%;
                margin-bottom: 26px;
                li{
                    @include texto ($textSmall, 2.5rem, $fontRegular, start);
                    width: 100%;
                }
            }
            div{
                width: 100%;
                div{
                    width: 100%;
                    pre{
                        width: 100%;
                        overflow: auto;
                        code{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}

/* Media queries */
@media (width >= 992px){    
    .mainPost{
        padding: $pLarge 20% 0 20%;
        .contenedorPost{
            background-color: map-get($map: $paleta, $key: 'cards');
            border-radius: $borderRadiusLg;
            box-shadow: $sombra;
            padding-bottom: $pMedium;
            .portadaPost{
                padding: 0;
                img{
                    width: 100%;
                    box-shadow: $sombra;
                    border-radius: $borderRadiusLg
                }
            }
            .contenidoPost{
                @include flex (column, center, center);
                margin: 0$mLarge $mMedium$mLarge;
                h1{
                    margin: 0 0 $mMedium 0;
                }
                h2{
                    margin: $mLarge 0 $mMedium 0;
                }
                p{
                    width: 100%;
                    line-height: 2.5rem;
                }
                figure{
                    padding: 0 3%;
                    div{
                        @include flex (column, center, center);
                        img{
                            width: 90%;
                            margin: $mXs 0;
                        }
                        figcaption{
                            width: 90%;
                        }
                    }
                }
            }
        }
    }
}